import { FC } from 'react';

import { Avatar, Box, Grid } from '@mui/material';
import { getFormattedDateAndTime } from '@packages/helpers';
import { StyledRootProps } from '@packages/types';
import { Link } from 'components/links/Link';
import { REVIEW_STARS_COUNT } from 'components/sections/ListingReviewsSection';
import { Typography } from 'components/Typography';
import { useTranslate } from 'hooks/index';
import WithTitleTemplate from 'templates/WithTitleTemplate';
import { ReviewItemType } from 'types/index';

import { styled } from '../../../theme';
import { RatingView } from '../RatingView';
import { TextMoreView } from '../TextMoreView/TextMoreView';

export type ReviewItemViewProps = StyledRootProps &
  Omit<ReviewItemType, 'id'> & {
    listingHref?: string;
  };

const PREFIX = 'ReviewItem';

export const ReviewItemClasses = {
  date: `${PREFIX}-date`,
  ratingWrapper: `${PREFIX}-ratingWrapper`,
  ratingValue: `${PREFIX}-ratingValue`,
};

export const StyledReviewItemView = styled(Box, {
  name: 'StyledReviewItemView',
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    [`& .${ReviewItemClasses.date}`]: {
      ...theme.typography.small1,
      color: theme.palette.text.light,
      lineHeight: theme.typography.pxToRem(15),
    },

    [`& .${ReviewItemClasses.ratingWrapper}`]: {
      height: 18, // prevents shrinking
      lineHeight: theme.typography.pxToRem(18),
    },

    [`& .${ReviewItemClasses.ratingValue}`]: {
      ...theme.typography.body1,
    },
  };
});

export const ReviewItemView: FC<ReviewItemViewProps> = (props) => {
  const {
    firstName = '?',
    avatarSrc,
    comment = '',
    date = new Date(),
    rating,
    place,
    listingHref,
    ...rest
  } = props;

  const translate = useTranslate({ basePath: 'custom' });

  const { date: formattedDate } = getFormattedDateAndTime({
    date,
    dateFormat: 'dd MMM yyyy',
  });

  return (
    <StyledReviewItemView {...rest}>
      <WithTitleTemplate
        sx={{ mb: 2 }}
        title={
          <Typography
            withTooltip
            variant="h6"
            overflow="hidden"
            textOverflow="ellipsis"
            whiteSpace="nowrap"
          >
            {firstName}
          </Typography>
        }
        subtitle={
          <>
            {!!rating && (
              <Box className={ReviewItemClasses.ratingWrapper}>
                <RatingView
                  typographyProps={{ className: ReviewItemClasses.ratingValue }}
                  iconSx={{ width: 16, height: 16 }}
                  rating={rating}
                  space={0.5}
                  starsCount={REVIEW_STARS_COUNT}
                />
              </Box>
            )}
          </>
        }
      >
        <Avatar src={avatarSrc}>{firstName[0]?.toUpperCase()}</Avatar>
      </WithTitleTemplate>

      {/* always show in LTR direction */}
      <TextMoreView text={comment} dir="ltr" />
      <Grid container mt={2} justifyContent="space-between">
        <Grid item xs={3}>
          <Typography className={ReviewItemClasses.date} color="text.light">
            {formattedDate}
          </Typography>
        </Grid>

        {place && listingHref && (
          <Grid item container xs={9} justifyContent="flex-end">
            <Typography color="text.light" component="span">
              {translate('stayed_in')}{' '}
              <Link
                target="_blank"
                href={listingHref}
                color="text.light"
                sx={{ fontWeight: 700, wordBreak: 'break-word' }}
              >
                {place}
              </Link>
            </Typography>
          </Grid>
        )}
      </Grid>
    </StyledReviewItemView>
  );
};

export default ReviewItemView;
