import { FC, ReactNode } from 'react';

import { Box, Grid } from '@mui/material';
import { Identifier, StyledRootProps } from '@packages/types';
import { Typography } from 'components/Typography';
import { useGetInfiniteReviews, useTranslate } from 'hooks/index';
import { RatingItem } from 'types/index';
import RatingView from 'views/RatingView';
import { ReviewsSectionView } from 'views/ReviewsSectionView';

export interface ReviewsSectionProps extends StyledRootProps {
  id: Identifier;
  ratingData?: Array<RatingItem>;
  maxReviews?: number;
  children?: ReactNode;
  averageRating?: number;
  reviewsCount?: number;
}

export const REVIEW_STARS_COUNT = 5;

export const ListingReviewsSection: FC<ReviewsSectionProps> = (props) => {
  const {
    id,
    ratingData = [],
    averageRating,
    reviewsCount,
    maxReviews = 6,
    className,
    sx,
    children,
  } = props;
  const translate = useTranslate({ basePath: 'pages.listing' });

  const {
    data: reviewsData,
    isLoading,
    hasNextPage,
    fetchNextPage,
    isFetchingNextPage,
  } = useGetInfiniteReviews({ listingId: id });

  const finalReviewsData = reviewsData?.map((reviewData) => ({
    ...reviewData,
    firstName: reviewData.firstName || translate.root('custom.guest'),
  }));

  return (
    <Box>
      <Grid container alignItems="center" gap={1} mb={{ xs: 1, sm: 2 }}>
        <Typography variant="h4">{translate('titles.review')}</Typography>
        {!!averageRating && (
          <RatingView
            rating={averageRating}
            reviewsCount={reviewsCount}
            starsCount={REVIEW_STARS_COUNT}
            typographyProps={{
              variant: 'body2',
            }}
          />
        )}
      </Grid>

      {finalReviewsData?.length ? (
        <ReviewsSectionView
          ratingData={ratingData}
          reviewsData={finalReviewsData}
          maxReviews={maxReviews}
          isLoading={isLoading}
          hasMore={hasNextPage}
          onLoadMore={fetchNextPage}
          className={className}
          sx={sx}
          isFetchingNextPage={isFetchingNextPage}
        >
          {children}
        </ReviewsSectionView>
      ) : (
        <Typography variant="subtitle1" sx={{ mt: 3 }}>
          {translate.root('custom.no_reviews')}
        </Typography>
      )}
    </Box>
  );
};
