import { FC } from 'react';

import { Box, Typography } from '@mui/material';
import { StyledRootProps } from '@packages/types';
import { clsx } from 'clsx';
import { ShowMoreWrapper, ShowMoreWrapperProps } from 'components/layouts/ShowMoreWrapper';
import { styled } from 'theme/styled';

export interface TextMoreViewProps
  extends Omit<ShowMoreWrapperProps, 'items' | 'maxItems' | 'children'>,
    StyledRootProps {
  maxTextLength?: number;
  text: string;
  dir?: 'ltr' | 'rtl';
}

const PREFIX = 'TextMoreView';

export const TextMoreClasses = {
  text: `${PREFIX}-text`,
  button: `${PREFIX}-button`,
};

export const StyledTextMoreView = styled(Box, {
  label: `Sapiens--${PREFIX}`,
  name: `Styled${PREFIX}`,
})(({ theme }) => {
  return {
    [`& .${TextMoreClasses.text}`]: {
      ...theme.typography.subtitle1,
      wordBreak: 'break-word',
    },

    [`& .${TextMoreClasses.button}`]: {
      height: 'auto',
      margin: theme.spacing(0, 0, 0, 0),
      padding: 0,

      '&:hover': {
        background: 'transparent',
        textDecoration: 'none',
      },
    },
  };
});

export const TextMoreView: FC<TextMoreViewProps> = (props) => {
  const { text, maxTextLength = 165, slotProps, sx, className, dir, ...rest } = props;

  const { toggleButton: toggleButtonProps } = slotProps || {};

  const { className: toggleButtonClassName, ...toggleButtonRestProps } = toggleButtonProps || {};

  const chars = text.split('');

  return (
    <StyledTextMoreView sx={sx} className={className}>
      <ShowMoreWrapper<string>
        items={chars}
        maxItems={maxTextLength}
        slotProps={{
          toggleButton: {
            className: clsx(TextMoreClasses.button, toggleButtonClassName),
            disableRipple: true,
            ...toggleButtonRestProps,
          },
        }}
        {...rest}
      >
        {({ items, expanded, isItemsMoreThanMax }) => {
          const joinedText = items.join('');

          return (
            <>
              {joinedText.split('\n').map((line, index, array) => {
                const isTextOverflow =
                  array.length - 1 === index && !expanded && isItemsMoreThanMax;
                const lastChar = line.slice(-1);
                const overflowingText = lastChar === '.' ? '..' : '...';

                const finalText = isTextOverflow ? line + overflowingText : line;

                return (
                  <Typography key={index} dir={dir} className={TextMoreClasses.text}>
                    {finalText}
                  </Typography>
                );
              })}
            </>
          );
        }}
      </ShowMoreWrapper>
    </StyledTextMoreView>
  );
};

export default TextMoreView;
