import { FC } from 'react';

import { Theme, useMediaQuery } from '@mui/material';
import { ModalBase, ModalBaseProps } from 'modals/ModalBase';

export type ReviewsModalProps = ModalBaseProps;

export const ReviewsModal: FC<ReviewsModalProps> = (props) => {
  const { children, ...rest } = props;

  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('md'));

  return (
    <ModalBase fullScreen={isMobile} maxWidth="md" PaperProps={{ sx: { width: 1 } }} {...rest}>
      {children}
    </ModalBase>
  );
};
