import { FC, ReactNode, useCallback } from 'react';

import { Box, Divider, Grid, Theme, useMediaQuery } from '@mui/material';
import { useBoolean } from '@packages/helpers';
import { StyledRootProps } from '@packages/types';
import Button from 'buttons/Button';
import { Loader } from 'custom/Loader';
import { useIntersectionObserver, useTranslate } from 'hooks/index';
import { ReviewsModal } from 'modals/ReviewsModal';
import { GridItem } from 'templates/GridItem';
import { styled } from 'theme';
import { RatingItem, ReviewItemType } from 'types/index';
import RatingBarView from 'views/RatingBarView';
import { ReviewItemView } from 'views/ReviewItemView/ReviewItemView';

const PREFIX = 'ReviewsSectionView';

const StyledReviewsSection = styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    width: '100%',
  };
});

export interface ReviewsSectionViewProps extends StyledRootProps {
  ratingData: Array<RatingItem>;
  reviewsData: Array<ReviewItemType>;
  maxReviews: number;
  onLoadMore?: () => void;
  isLoading?: boolean;
  isFetchingNextPage?: boolean;
  hasMore?: boolean;
  children?: ReactNode;
}

export const ReviewsSectionView: FC<ReviewsSectionViewProps> = (props) => {
  const {
    reviewsData,
    ratingData,
    maxReviews,
    className,
    sx,
    onLoadMore,
    isLoading,
    hasMore,
    isFetchingNextPage,
  } = props;

  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('sm'));

  const translate = useTranslate({ basePath: 'buttons' });

  const { value: isModalOpen, setTrue: openModal, setFalse: closeModal } = useBoolean(false);

  const buttonsTranslationOptions = {
    items: translate.root('custom.review', { count: 2 }),
  };

  const truncatedReviews = reviewsData.slice(0, maxReviews);
  const isReviewsMoreThanMax = reviewsData.length > maxReviews || hasMore;

  const { targetElementRef } = useIntersectionObserver({
    onIntersection: onLoadMore,
    isEnabled: !isLoading && hasMore && !!onLoadMore,
    rootMargin: '50px',
  });

  const renderItems = useCallback(
    ({ items, enableLoadMore }: { items: ReviewItemType[]; enableLoadMore?: boolean }) => {
      return (
        <Grid container spacing={3}>
          {items.map((reviewItem, index) => {
            const { id, ...restReviewItemProps } = reviewItem;

            const isLastRow = isMobile ? index === items.length - 1 : index >= items.length - 2;
            const isLastElement = index === items.length - 1;

            const isAttachTargetRef = isLastElement && enableLoadMore;

            return (
              <GridItem
                key={id}
                ref={isAttachTargetRef ? targetElementRef : undefined}
                sm={6}
                display="flex"
                justifyContent="space-between"
                flexDirection="column"
              >
                <ReviewItemView {...restReviewItemProps} />

                {!isLastRow && <Divider sx={{ mt: { xs: 2, md: 3 } }} />}
              </GridItem>
            );
          })}
        </Grid>
      );
    },
    [isMobile, targetElementRef],
  );

  return (
    <StyledReviewsSection component="section" className={className} sx={sx}>
      <Grid container spacing={3} mb={4}>
        {ratingData.map((ratingItem) => {
          const { title, value } = ratingItem;
          return (
            <GridItem key={title} sm={6}>
              <RatingBarView label={title} value={value} />
            </GridItem>
          );
        })}
      </Grid>
      {renderItems({ items: truncatedReviews })}

      {isReviewsMoreThanMax && (
        <>
          <Button
            onClick={openModal}
            variant={isMobile ? 'outlined' : 'text'}
            fullWidth={isMobile}
            sx={{
              display: 'block',
              mx: 'auto',
              mt: 4,
              textTransform: 'lowercase',
              ':first-letter': { textTransform: 'uppercase' },
            }}
            color="secondary"
          >
            {translate('show_all_items', buttonsTranslationOptions)}
          </Button>
          <ReviewsModal
            title={translate.root('pages.listing.titles.review')}
            open={isModalOpen}
            onClose={closeModal}
          >
            {renderItems({ items: reviewsData, enableLoadMore: true })}
            {isFetchingNextPage ? <Loader sx={{ my: 3 }} /> : null}
          </ReviewsModal>
        </>
      )}
    </StyledReviewsSection>
  );
};
