import { FC } from 'react';

import { Box, LinearProgress, Typography } from '@mui/material';
import { getPercentValue } from '@packages/helpers';

import StyledRoot, { RatingBarClasses } from './StyledRatingBarView';
import { RatingBarViewProps } from './types';

const RatingBarView: FC<RatingBarViewProps> = (props) => {
  const { label, value, maxProgressValue = 5, ...rest } = props;
  const { className, sx, ...restLinearProgressProps } = rest;

  return (
    <StyledRoot className={className} sx={sx}>
      <Box className={RatingBarClasses.labelWrapper}>
        <Typography className={RatingBarClasses.text} title={label}>
          {label}
        </Typography>
      </Box>

      <Box className={RatingBarClasses.progressWrapper}>
        <LinearProgress
          {...restLinearProgressProps}
          className={RatingBarClasses.progress}
          variant="determinate"
          value={getPercentValue({ value, maxValue: maxProgressValue })}
        />
      </Box>

      <Box className={RatingBarClasses.ratingWrapper}>
        <Typography className={RatingBarClasses.text}>{value < 0 ? 0 : value}</Typography>
      </Box>
    </StyledRoot>
  );
};

export default RatingBarView;
