import { Children, useMemo } from 'react';

import { Grid, GridProps } from '@mui/material';

import { GridItem } from '../GridItem';

export type GridContainerProps = GridProps & {
  columnsOrSizes?: 1 | 2 | 3 | 4 | 6 | 12 | Array<number>;
};

/**
 * Do NOT pass margin/padding, because Grid container uses it
 */
export function GridContainer(props: GridContainerProps) {
  const { children, columnsOrSizes } = props;

  const resultChildren = useMemo(() => {
    if (typeof columnsOrSizes === 'undefined') return children;
    if (Array.isArray(columnsOrSizes)) {
      return Children.map(children, (child, index) => {
        const md = columnsOrSizes[index] || 12;
        return <GridItem md={md}>{child}</GridItem>;
      });
    }
  }, [children, columnsOrSizes]);

  return (
    <Grid container rowSpacing={1} columnSpacing={3} {...props}>
      {resultChildren}
    </Grid>
  );
}

export default GridContainer;
